<!-- 
    jsonEidtor

    #props 
    height - eidtor500
    data - editor  {}

    #methods 
    onChange - 

 -->
<template>
    <div ref="jsonEditor" class="json-editor" :style="{height: height+'px'}">
    </div>
</template>
<script>
import JSONEditor from "jsoneditor";
export default {
    name: "json-editor",
    props: {
        height: {
            type: Number,
            default: 500
        },
        data: {
            type: Object,
            default: function() {
                return {};
            }
        }
    },
    watch: {
        // data: {
        //     handler(cur, old) {
        //         debugger
        //         this.updateEditor()
        //     },
        //     deep: true
        // }
        data(cur, old) {
            this.updateEditor();
            // jsononchangecurrentjsoncurrentjson
            this.$emit("onChange", this.data);
        }
    },
    methods: {
        // jsoneditor
        initJsonEditor() {
            // let JSONEditor = null;
            // if (typeof window !== "undefined") {
            //     JSONEditor = require("jsoneditor");
            // }
            let self = this;
            let options = {
                mode: "code",
                modes: ["code", "text", "view"],
                ace: false,
                onEditable: function(node) {
                    if (!node.path) {
                        // In modes code and text, node is empty: no path, field, or value
                        // returning false makes the text area read-only
                        return true;
                    }
                },
                onError: function(err) {
                    alert(err.toString());
                },
                onChange: function() {
                    let json = null;

                    // jsonemit onchange
                    try {
                        json = self.getEditorJson();
                    } catch (err) {
                        return;
                    }

                    self.$emit("onChange", json);
                }
                // onChangeText: function (json) {
                //     //console.log( json );
                //     self.$emit("onChange", json)
                // }
            };
            this.editor = new JSONEditor(this.$refs.jsonEditor, options);
        },
        updateEditor() {
            this.editor.set(this.data);
        },
        getEditorJson() {
            return this.editor.get();
        }
    },
    mounted() {
        this.initJsonEditor();
    }
};
</script>
<style>
@import "../../node_modules/jsoneditor/dist/jsoneditor.min.css";
</style>